.promo {
    min-height: 100vh;
    position: relative;
    background: url('../img/main_bg.svg') no-repeat 50%/cover;
    padding-top: 205px;
    &__title {
        width: 535px;
        margin-top: 10px;
    }
    &__subtitle {
        position: relative;
        padding-left: 10px;
        z-index: 2;
        @include before_circle;
    }
    &__btns {
        margin-top: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 250px;
    }
    &__link {
        color: rgb(255, 255, 255);
        font-weight: bold;
        &:hover {
            color: rgb(255, 255, 255);
            text-decoration: none;
            opacity: 0.7;
        }
        &:active {
            opacity: 0.9;
        }
    }
}

.promo__subtitle {
    color: rgb(255, 255, 255);
}

